$color-primary: #155263;
$color-secondary: #ff6f3c;
$watches-size: 400px;
$watches-size-center: $watches-size / 20;
$clock-hand-size: $watches-size-center / 2;
$clock-hand-size-radius: $clock-hand-size;
$clock-hand-size-minute: $watches-size / 2.5;
$clock-hand-size-second: $watches-size / 2;
$clock-hand-time-second: 60;
$clock-hand-time-minute: $clock-hand-time-second * $clock-hand-time-second;
